<template>
  <v-container>
    <!-- スタミナクエ 特効
    <v-text-field
        label="星2素材"
        class="pa-0 my-1"
        outlined
        v-model.number="dateDATA"
    ></v-text-field> -->
    <template>
    <span class="text-h6">アイテム所持数</span>
    <v-row class="pa-0 mt-n1 mb-n9">
      <v-col cols="6" sm="6" md="3">
        <v-text-field
          label="星2素材"
          outlined
          v-model.number="inputData.ownedItem.rare1"
        ></v-text-field>
      </v-col>

      <v-col cols="6" sm="6" md="3">
        <v-text-field
          label="星3素材"
          outlined
          v-model.number="inputData.ownedItem.rare2"
        ></v-text-field>
      </v-col>
    </v-row>

    <v-row class="pa-0 mb-n5">
      <v-col md="6">
        <v-text-field
          label="チケット"
          outlined
          v-model.number="inputData.ownedItem.ticket"
        ></v-text-field>
      </v-col>
    </v-row>
    </template>

    <template>
    <span class="text-h6">特効数</span>
    <v-row class="pa-0 ml-0" style="font-size: 14px"> スタミナ </v-row>
    <v-row class="pa-0 mb-n9">
      <v-col cols="6" sm="6" md="3">
        <v-text-field
          label="星2素材"
          outlined
          v-model.number="inputData.bonus.stamina.rare1"
        ></v-text-field>
      </v-col>

      <v-col cols="6" sm="6" md="3">
        <v-text-field
          label="星3素材"
          outlined
          v-model.number="inputData.bonus.stamina.rare2"
        ></v-text-field>
      </v-col>
    </v-row>

    <v-row class="pa-0 ml-0" style="font-size: 14px"> チケット </v-row>

    <v-row class="pa-0 mb-n9">
      <v-col cols="6" sm="6" md="3">
        <v-text-field
          label="星2素材"
          outlined
          v-model.number="inputData.bonus.ticket.rare1"
        ></v-text-field>
      </v-col>

      <v-col cols="6" sm="6" md="3">
        <v-text-field
          label="星3素材"
          outlined
          v-model.number="inputData.bonus.ticket.rare2"
        ></v-text-field>
      </v-col>
    </v-row>

    <v-row class="pa-0 mb-n7 mt-n4">
      <v-col md="6">
        <v-btn
          outlined
          color="light-blue darken-2"
          @click="staminaBonusToTickets"
        >
          チケットの特効をスタミナと同じにする
        </v-btn>
      </v-col>
    </v-row>

    <v-row class="pa-0 my-n4">
      <v-col md="6">
        <v-checkbox
          label="チケットクエは周回しない"
          v-model.number="inputData.noPlayTicketQuest"
        ></v-checkbox>
      </v-col>
    </v-row>
    </template>

  <template>
    <span class="text-h6 mb-n10">必要個数</span>
    <v-row class="pa-0">
      <v-col cols="6" sm="6" md="3">
        <v-text-field
          label="星2素材"
          outlined
          v-model.number="inputData.Objective.rare1"
        ></v-text-field>
      </v-col>

      <v-col cols="6" sm="6" md="3">
        <v-text-field
          label="星3素材"
          outlined
          v-model.number="inputData.Objective.rare2"
        ></v-text-field>
      </v-col>
    </v-row>
  </template>

<template>
    <v-row class="text-h6 mt-n8">
      <v-col>
        <span>
          星2素材回収完了までに <br />
          必要スタミナ: {{ this.output_necessaryStamina }}<br />
          必要周回数(スタミナ): {{ this.output_necessaryTimeToQuest_Stamina }}
          <br />
          必要周回数(チケット): {{ this.output_necessaryTimeToQuest_Tickets }}
          <br />
          <br />星3素材回収完了までに <br />
          必要スタミナ: {{ this.output_necessaryStamina2 }}<br />
          必要周回数(スタミナ): {{ this.output_necessaryTimeToQuest_Stamina2 }}
          <br />
          必要周回数(チケット): {{ this.output_necessaryTimeToQuest_Tickets2 }}
          <br />
        </span>
      </v-col>
    </v-row>
</template>
  </v-container>
</template>


<script>
export default {
  name: "CalcPanel",

  data: () => ({
    inputData: {
      noPlayTicketQuest: false,
      ownedItem: {
        rare1: undefined,
        rare2: undefined,
        ticket: undefined,
      },
      bonus: {
        ticket: {
          rare1: undefined,
          rare2: undefined,
        },
        stamina: {
          rare1: undefined,
          rare2: undefined,
        },
      },
      Objective: {
        rare1: undefined,
        rare2: undefined,
      },
    },
  }),

  props: {
    EventData: {
      type: Object,
    },
    themeColor: {
      type: Function,
    },
    settings: {
      type: Object,
    },
  },

  computed: {
    output_necessaryStamina() {
      return this.necessaryTimeOrStm(1, "stamina");
    },
    output_necessaryTimeToQuest_Stamina() {
      return this.necessaryTimeOrStm(1, "TimeStamina");
    },
    output_necessaryTimeToQuest_Tickets() {
      return this.necessaryTimeOrStm(1, "TimeTickets");
    },

    output_necessaryStamina2() {
      return this.necessaryTimeOrStm(2, "stamina");
    },
    output_necessaryTimeToQuest_Stamina2() {
      return this.necessaryTimeOrStm(2, "TimeStamina");
    },
    output_necessaryTimeToQuest_Tickets2() {
      return this.necessaryTimeOrStm(2, "TimeTickets");
    },

    // NeedItem(rare){
    //   return this.inputData.Objective[`rare${rare}`] - this.inputData.ownedItem[`rare${rare}`]
    // }
  },

  mounted: function () {
    // console.log(this.themeColor())
  },

  methods: {
    staminaBonusToTickets() {
      this.inputData.bonus.ticket.rare1 = this.inputData.bonus.stamina.rare1;
      this.inputData.bonus.ticket.rare2 = this.inputData.bonus.stamina.rare2;
    },

    necessaryTimeOrStm(rare, type) {
      // console.log(this.EventData)
      let input = this.inputData;
      let EventData = this.EventData;
      let RequiredNumOfItem = this.NeedItem(rare);

      // if(!(input.bonus["stamina"][`rare${rare}`] && input.bonus["tickets"][`rare${rare}`] && input.ownedItem.ticket) && (Objective === 0)) {
      //   return 0
      // }
      // console.log(input.bonus["stamina"])

      let gotItem_Per_StmQuest =
        EventData["stamina"][`rare${rare}`]["itemNums"] +
        EventData["stamina"][`rare${rare}`]["itemGroups"] *
          (input.bonus["stamina"][`rare${rare}`] || 0);

      let gotItem_Per_TicketsQuest =
        EventData["tickets"][`rare${rare}`]["itemNums"] +
        EventData["tickets"][`rare${rare}`]["itemGroups"] *
          (input.bonus["ticket"][`rare${rare}`] || 0);

      let NumOfPlayStmQuest = (() => {
        if (input.noPlayTicketQuest) {
          return Math.ceil(RequiredNumOfItem / gotItem_Per_StmQuest);
        } else {
          if(RequiredNumOfItem - (input.ownedItem.ticket || 0) * gotItem_Per_TicketsQuest <= 0){
            return 0;
            // return Math.ceil(RequiredNumOfItem / gotItem_Per_TicketsQuest)
          }else {
            return Math.ceil(
                (RequiredNumOfItem -
                    (input.ownedItem.ticket || 0) * gotItem_Per_TicketsQuest) /
                (gotItem_Per_StmQuest +
                    EventData["dropTicketsRate"] * gotItem_Per_TicketsQuest)
            );
          }
        }
      })();
      // // for(let i=0;;i++){
      //   let GotItemFromStmQuest = gotItem_Per_StmQuest * i
      //
      //   let NumOfTickets = (input.noPlayTicketQuest) ? 0 : Math.floor( * i) +
      //   let GotItemFromTickets = gotItem_Per_TicketsQuest * (NumOfTickets);

      // if(RequiredNumOfItem <= GotItemFromStmQuest + GotItemFromTickets){

      switch (type) {
        case "stamina":
          return NumOfPlayStmQuest < 0 ? 0 : NumOfPlayStmQuest * 25;
        case "TimeStamina":
          return NumOfPlayStmQuest < 0 ? 0 : NumOfPlayStmQuest;
        case "TimeTickets":
          return (()=>{
            if(input.noPlayTicketQuest || NumOfPlayStmQuest < 0){
              return  "0 + 0";
            }else if(RequiredNumOfItem - (input.ownedItem.ticket || 0) * gotItem_Per_TicketsQuest <= 0){
              return `0 + ${Math.ceil(RequiredNumOfItem / gotItem_Per_TicketsQuest)}`
            }else{
              return `${cutOffDecimalNum(
                  NumOfPlayStmQuest * EventData["dropTicketsRate"],
                  2
              )} + ${input.ownedItem.ticket || 0}`
            }
          })()

;
      }

      function cutOffDecimalNum(num, digits) {
        return Math.floor(num * (10 ^ digits)) / (10 ^ digits);
      }
      // }

      //   if(i > 2000){
      //     alert("無限ループ回避のため停止\nきちんとしたアルゴリズムで書けないorz\nチケットの周回枚数は小数で出力してもいいならforループいらないけど小数は切捨てしたい場合のいい案が思いつかない")
      //     return  0;
      //   }
      // }
    },

    NeedItem(rare) {
      let NeedItem = (this.inputData.Objective[`rare${rare}`] || 0) - (this.inputData.ownedItem[`rare${rare}`] || 0);
      return NeedItem >= 0 ? NeedItem : 0;
    },
  },
};
</script>
